/* ==========================================================================
   Progress Bar shortcode style - begin
   ========================================================================== */

.mkdf-progress-bar {
    @include mkdfRelativeHolderLayout();

    &.mkdf-pb-percent-floating {
        width: 100%;
        height: 100%;

        .mkdf-pb-percent {
            position: absolute;
            left: 0;
            right: auto;
            bottom: 0;
            @include mkdfTransform(translateX(-50%));
        }
    }

    .mkdf-pb-title-holder {
        position: relative;
        margin: 10px 0;
        
        .mkdf-pb-title {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            z-index: 100;
        }
    }
	
    .mkdf-pb-percent {
        position: absolute;
        right: 0;
        bottom: 1px;
        width: auto;
	    display: inline-block;
	    vertical-align: middle;
	    opacity: 0;
	    z-index: 10;
        font-family: $default-heading-font;
        color: $default-heading-color;
        font-size: 21px;

        &:after {
            content: '%';
        }
    }
    
    .mkdf-pb-content-holder {
        position: relative;
        height: 6px;
        overflow: hidden;
        background-color: #ebebeb;
        
        .mkdf-pb-content {
            height: 6px;
            max-width: 100%;
            overflow: hidden;
            background-color: $first-main-color;
        }
    }
}
/* ==========================================================================
   Progress Bar shortcode style - end
   ========================================================================== */